.sectionHeader_burger {
  position: relative;
  top: 0;
  display: none;
  align-items: center;
  justify-content: center;
}
.sectionHeader_burger .burger {
  margin-top: 20px;
  width: 100px;
  height: auto;
  border: none;
}
.sectionHeader_burger .burger-menu_button {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 100;
  width: 32px;
  height: 32px;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  background-color: transparent;
  width: 40px;
  height: 40px;
  position: relative;
  cursor: pointer;
  background-color: transparent;
  width: 40px;
  height: 40px;
  position: relative;
  cursor: pointer;
}
.sectionHeader_burger .burger-menu_button::after, .sectionHeader_burger .burger-menu_button::before {
  content: "";
  width: 22px;
  height: 2px;
  background-color: #fff;
  position: absolute;
  top: 15px;
  left: 9px;
}
.sectionHeader_burger .burger-menu_button::after {
  top: auto;
  bottom: 15px;
  width: 11px;
}
.sectionHeader_burger .burger-menu_button::after, .sectionHeader_burger .burger-menu_button::before {
  transition: all 0.5s;
  content: "";
  width: 22px;
  height: 2px;
  background-color: #fff;
  position: absolute;
  top: 15px;
  left: 9px;
}
.sectionHeader_burger .burger-menu_button::after {
  top: auto;
  bottom: 15px;
  width: 11px;
}
.sectionHeader_burger .burger-menu_button:hover .burger-menu_lines {
  filter: brightness(0.7);
}
.sectionHeader_burger .burger-menu_button:hover .burger-menu_lines::before,
.sectionHeader_burger .burger-menu_button:hover .burger-menu_lines::after,
.sectionHeader_burger .burger-menu_button:hover .burger-menu_lines {
  background-color: rgba(0, 0, 0, 0.7);
}
.sectionHeader_burger .burger-menu_button.burger-menu_button_active:hover .burger-menu_lines {
  background-color: transparent;
}
.sectionHeader_burger .burger-menu_active .burger-menu_button::before {
  top: 19px;
  transform: rotate(45deg);
}
.sectionHeader_burger .burger-menu_active .burger-menu_button::after {
  bottom: 19px;
  width: 22px;
  transform: rotate(-45deg);
}
.sectionHeader_burger .burger-menu_nav {
  padding: 60px 30px 20px 30px;
  position: absolute;
  top: 0;
  right: -15px;
  z-index: 70;
  display: flex;
  flex-flow: column;
  background-color: #fff;
  overflow-y: auto;
  height: 372px;
  width: 323px;
  top: -400px;
  -webkit-transition: 0.8s;
  -moz-transition: 0.8s;
  -o-transition: 0.8s;
  transition: 0.8s;
}
.sectionHeader_burger .burger-menu_active .burger-menu_nav {
  top: -55px;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}
.sectionHeader_burger .burger-menu_link {
  font-family: Raleway, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 120%;
  text-decoration: none;
