@function toRem($value) {
    $remValue   : calc($value / 16) + rem;
    // $remValue: $value + px; 
    @return $remValue;
}

@mixin RubikRegular($size_px) {
    font-family: 'Rubik', sans-serif;
    font-weight: 400;
    font-size  : toRem($size_px);
    font-style : normal;
}
@mixin RubikMedium($size_px) {
    font-family: 'Rubik', sans-serif;
    font-weight: 500;
    font-size  : toRem($size_px);
    font-style : normal;
}
@mixin RubikSemiBold($size_px) {
    font-family: 'Rubik', sans-serif;
    font-weight: 600;
    font-size  : toRem($size_px);
    font-style : normal;
}
@mixin RubikBold($size_px) {
    font-family: 'Rubik', sans-serif;
    font-weight: 700;
    font-size  : toRem($size_px);
    font-style : normal;
}
@mixin RubikBlackItalic($size_px) {
    font-family: 'Rubik', sans-serif;
    font-weight: 900;
    font-size  : toRem($size_px);
    font-style : italic;
}
@mixin InterRegular($size_px) {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
