@import 'color.scss';

.sectionWantSee {
    height: 857px;
    background-color: $grey_color;
    padding: 150px 0;
    .sectionInner {
        height: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }
    &_content {
        &-title {
            @include RubikBold(48);
            color: #fff;
            width: 599px;
            margin: 0 0 60px 0;
            span{
                color: $secondary_color;
            }
        }
        &-text {
            @include RubikRegular(18);
            width: 393px;
            margin: 0 0 60px 0;
            color: #fff;
        }
        &-requestCall {
            width: 330px;
            height: 80px;
        }
    }
    &_img {
        &-item{
            width: 100%;
        }
    }
}